<template>
    <el-row>
        <el-row>
            <el-col :span="20">
                <el-row style="margin-bottom:6px; padding:0 60px;">
                    <el-input v-model="queryData.code" placeholder="编码" :maxlength="20" size="mini" class="inputwinth150">
                        <template slot="prepend">编码</template>
                    </el-input>
                    <el-input v-model="queryData.name" placeholder="名称" :maxlength="20" size="mini" class="inputwinth150">
                        <template slot="prepend">名称</template>
                    </el-input>
                    <el-radio v-model="queryData.is_Enable" :label="1">启用</el-radio>
                    <el-radio v-model="queryData.is_Enable" :label="0">弃用</el-radio>
                </el-row>
                <el-row>
                
                
                
                </el-row>
            </el-col>
            <el-col :span="4">
                <el-button size="mini" @click="cleanData" icon="fa fa-eraser"> 清 空</el-button>
                <el-button size="mini" @click="getPageData" icon="el-icon-search">查 询</el-button>
            </el-col>
        </el-row>
        
        
        
        <el-table :data="tableData" :row-class-name="tableRowClassName" size="mini" v-loading="loadingdata" element-loading-text="正在加载中..." border>
            <el-table-column type="index" label="序号" width="50">
            </el-table-column>
            <el-table-column prop="code" label="编码">
            </el-table-column>
            <el-table-column prop="name" label="名称">
            </el-table-column>
            <el-table-column prop="code" label="编码">
            </el-table-column>
            <el-table-column prop="name" label="姓名">
            </el-table-column>
            <el-table-column prop="is_Male" label="性别">
            </el-table-column>
            <el-table-column prop="idType" label="证件类型">
            </el-table-column>
            <el-table-column prop="idNum" label="证件号码">
            </el-table-column>
            <el-table-column prop="phoneNum" label="手机号码">
            </el-table-column>
            <el-table-column prop="mailbox" label="邮箱">
            </el-table-column>
            <el-table-column prop="homeAddr" label="家庭地址">
            </el-table-column>
            <el-table-column prop="education" label="学历">
            </el-table-column>
            <el-table-column prop="entryTime" label="入职时间">
            </el-table-column>
            <el-table-column prop="quitTime" label="离职时间">
            </el-table-column>
            <el-table-column prop="is_Quit" label="是否离职">
            </el-table-column>
            <el-table-column label="状态" width="70">
                <template slot-scope="prop">
                    <span v-html="prop.row.is_Enable==1?'启用':'弃用'"></span>
                </template>
            </el-table-column>
            <el-table-column label="操作" width="100">
                <template slot-scope="prop">
                    
                    
                </template>
            </el-table-column>
        </el-table>
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pIndex" :page-sizes="[10,20,50,100]" :page-size="pSize" layout="total, sizes, prev, pager, next, jumper" :total="totalNum">
        </el-pagination>
    </el-row>
</template>
<script>
   
   
   
    export default {
        components: {
    
    
    
        },
        data() {
            return {
                dialogShow: false,
                dialogTitle: "",
                dialogData: { id: 0, is_Enable: 1, is_Remove: 0 },
                queryData: {},
                moduleName: "person",
                pIndex: 1,
                pSize: 10,
                totalNum: 0,
                loadingdata: false,
                tableData: [],
                roleList: [],
                deptList: [],
                importExcelShow: false,
                exportExcelShow: false,
                exportPageStr: "",
                exportWhereStr: ""
            };
        },
        created() {
            //console.log("created");
        },
        mounted() {
            //console.log("mounted");
            this.getPageData();
        },
        methods: {
            cleanData() {
                this.queryData = {};
            },
            handleSizeChange(val) {
                this.pSize = val;
                this.getPageData();
            },
            handleCurrentChange(val) {
                this.pIndex = val;
                this.getPageData();
            },
            tableRowClassName(row, rowIndex) {
                if (row.is_Enable == 0) {
                    return "warning-row";
                }
                return "";
            },
            getPageData() {
                var condition = this.global.getcondition(this.queryData);
                var getpath =this.moduleName +"/findAll?pIndex=" +this.pIndex +"&pSize=" +this.pSize + condition;
                this.loadingdata = true;
                this.$ajax.get(getpath).then(res => {
                    this.loadingdata = false;
                    this.tableData = res.data;
                    this.totalNum = res.totalNum;
                  },error => {
                    this.loadingdata = false;
                });
            },
        }
    };
</script>
<style scoped>
</style>



